}

.calendar {
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  gap: 1em;
  font-size: 30px;
  display: grid;
}

.calendar__day {
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  background-color: #0006;
